  /*rounded switch-------------------------------------*/
.switch{
    position: relative;
    display: inline-block;
    width: 60px;
    height: 28px;
    margin-bottom: 0;
  }
  /*hide default html checkbox*/
  .switch input{
    position: absolute;
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    z-index: -1;
  }
  /*the slider*/
  .slider{
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
  }
  .slider:before{
    position: absolute;
    content: "";
    height: 21.4px;
    width: 21.4px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }
  input:checked + .slider{
    background-color: #F59D18;
  }
  input:focus + .slider{
    box-shadow: inset 0 1px 1px #ccc, 0 0 6px #bbb;
  }
  input:checked + .slider:before{
    -webkit-transform: translateX(30px);
    transform: translateX(30px);
  }
  /*rounded sliders*/
  .slider.round{
    border-radius: 34px;
  }
  .slider.round:before{
    border-radius: 50%;
  }
  
  /*end rounded switch---------------------------------*/

